.row {
	color: white;
	margin-left: 20px;
}
.row__posters {
	display: flex;
	overflow-y: hidden;
	-ms-overflow-style: none;
	scrollbar-width: none; 
	overflow-x: scroll;
	padding: 20px;
}

.row__poster::-webkit-scrollbar {
	display: none;
	background: transparent;
	width: 0;
}

.row__poster{
	object-fit: contain;
	width: 100%;
	max-height: 100px;
	margin-right: 10px;
	transition: transform 450ms;	
}
  
.row__poster:hover {
	transform: scale(1.08);

}

.row__posterLarge {
	max-height: 250px;
}
.row__posterLarge:hover {
	transform: scale(1.09);
	opacity: 1;

}